@use "sass:math";
@use "sass:list";

@use "uswds-core" as *;
@use "usa-button" as usabtn;

.usa-banner {
  @include typeset($theme-banner-font-family);
  @include border-box-sizing;
  background-color: color($theme-banner-background-color);

  @include at-media("tablet") {
    font-size: font-size($theme-banner-font-family, "3xs");
    padding-bottom: units(0);
  }

  // ! Stylelint will complain about BEM formatting.
  // Added grid classes in #5000 to remove layout grid dependency.
  // stylelint-disable selector-class-pattern
  .grid-row {
    @include grid-row;

    &.grid-gap-lg {
      @include grid-gap("lg");
    }

    .tablet\:grid-col-6 {
      @include at-media("tablet") {
        @include grid-col(6);
      }
    }
  }
  // stylelint-enable
}

.usa-banner__inner {
  @include add-responsive-site-margins;
  @include grid-container($theme-banner-max-width);
  @include grid-row;
  @include u-flex("align-center", "justify", "row", "no-wrap");
  min-height: size($theme-button-font-family, "2xs") + units(1) * 2;

  .usa-banner__language-toggle {
    @extend .usa-button;
    @extend .usa-button--nci-small;
    @extend .usa-button--secondary;
  }

  .usa-button {
    width: initial;
    margin: 0;
  }
}

.usa-banner__header {
  @include u-padding-y(1);
  font-size: font-size($theme-banner-font-family, 1);
  font-weight: font-weight("normal");
  @include u-maxw($theme-banner-max-width);
  @include u-margin-x("auto");
  min-height: units($size-touch-target);
  position: relative;

  @include at-media("tablet") {
    min-height: 0;
  }
}

.usa-banner__header-text {
  @include u-margin-y(0);
  font-size: font-size($theme-banner-font-family, 1);
  line-height: line-height($theme-banner-font-family, 2);
  &::before {
    background-image: url('#{$theme-image-path}/us_flag_small.png');
    content: "";
    display: block;
    background-size: 16px 11px;
    max-width: 16px;
    height: 11px;
  }
}

.usa-banner__header-text,
.usa-banner__header-text::before {
  @include u-width("full");
  @include u-display("flex");
  @include u-margin-right(1.5);
}

// Hide banner when printing page
@media print {
  .usa-banner,
  .usa-banner__header {
    display: none;
  }
}
