@use "../../settings" as *;
@use "./icon" as *;
@use "../../functions" as *;
@use "./screen-reader" as *;
@use "sass:meta" as meta;

$external-link-size: 1.75ex;

$icon-object: (
  "name": "launch",
  "color": currentColor,
  "height": $external-link-size,
  "svg-height": 24,
  "svg-width": 24,
  "position-x": center,
  "position-y": center,
);

// Places icon and applies additional styles from mixin
@mixin external-link($contrast-bg: 'default', $external-mixin: false) {
	display: inline;

	&::after {
		content: '';
		@include add-color-icon($icon-object, $contrast-bg);
		display: inline;
		margin-top: 0.7ex;
    margin-left: 0.25em;
    padding-left: 1.75ex;
    vertical-align: middle;
	}

	// Screen reader labels (English)
	&:lang(en) {
		&::before {
			@include sr-only;
			content: $theme-external-link-sr-label-tab-same;
		}

		&[target='_blank']::before {
			@include sr-only;
			content: $theme-external-link-sr-label-tab-new;
		}
	}

	// Screen reader labels (Spanish)
	&:lang(es) {
		&::before {
			@include sr-only;
			content: $theme-external-link-sr-label-tab-same-es;
		}

		&[target='_blank']::before {
			@include sr-only;
			content: $theme-external-link-sr-label-tab-new-es;
		}
	}

	// Applies the specific mixin coming from the component
	// (Additional styles to be applied to the external link element or icon)
	// Include ::after in the mixin being passed to this mixin to style the icon
	@if $external-mixin {
		@include meta.apply($external-mixin);
	}
}

// Mixin for checking if link is external and applying the appropriate styles
// If a selector is specified, apply external icon to that selector
// If an external mixin is specified, apply that mixin to the external icon
@mixin automatic-external-link(
	$contrast-bg: 'default',
	$external-target: false,
	$external-mixin: false
) {

	$notDomains: '';
	//for each item in the list
	@each $domain in $theme-internal-domains {
		//generate a :not([ignored_item]) segment for each item in the ignore list and put them back to back
		@if $notDomains == '' {
			$notDomains: $notDomains + '[href*="#{$domain}"]';
		} @else {
			$notDomains: $notDomains + ',[href*="#{$domain}"]';
		}
	}
	$notDomains: $notDomains + ',[data-ncids-internal-link]';

	//output the full :not() rule including all ignored items
	//if a selector is specified, apply external icon to that selector
	//otherwise apply the icon to the link itself
	@if $theme-set-automatic-external-links-on {
		&[href^='http:'],
		&[href^='https:'] {
			@if $external-target {
				&:not(#{$notDomains}) {
					#{$external-target} {
						@include external-link($contrast-bg, $external-mixin);
					}
				}
			} @else {
				&:not(#{$notDomains}) {
					@include external-link($contrast-bg, $external-mixin);
				}
			}
		}
	}
}

// $margin-between is the space between the icon and the text
@mixin external-link-wrap-with-text($margin-between) {
  $icon-size-in-pixels: $theme-external-link-icon-size * 1px;
  // Display Block to wrap Icon with Text
  display: block !important;
  // Padding / Margin to prevent icon wrapping without text
  padding-right: calc($margin-between + $icon-size-in-pixels) !important;
  &::after {
    margin-right: calc(($margin-between + $icon-size-in-pixels) * -1) !important;
    margin-left: $margin-between !important;
  }
}
