@use "uswds-core" as *;

.usa-link {
  @include typeset-link;
}

// External link consider 'effortless style approach':
// [href^='http:']:not([href*='my-domain.com'])
// [href^='https:']:not([href*='my-domain.com'])

.usa-link--external {
  @include external-link();

  &.usa-link--alt {
    @include external-link($contrast-bg: "base-darker");
  }
}

a.usa-link:not(.usa-link--external) {
  @include automatic-external-link('default');

  &.usa-link--alt {
    @include automatic-external-link($contrast-bg: "base-darker");
  }
}
