@use "uswds-core" as *;

.usa-error-message {
  @include u-padding-y(0.5);
  color: color("error");
  display: block;
  font-weight: font-weight("bold");
}

.usa-error-message--nci-inverse {
  padding: units(1);
  color: color("white");
  background: color("error");
}

.usa-section--dark .usa-error-message {
  @extend .usa-error-message--nci-inverse;
}
